<template>
	<div>
		<template v-if="loadingGrants">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Grants</h3>
				</div>

			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Project Name</p>
						<p>Description</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="grantsLoaded && grants.length">
							<div v-for="grant in grants"
								:key="grant.id" class="activity">
								<div class="contacts--list__item">
									<label class="check">
										<input type="checkbox">
										<div class="check-mark"></div>
									</label>
									<p>{{ grant.project_name }}</p>
									<p class="info__address" v-html="grant.project_description"></p>
									<div>
										<router-link
											:to="{ name: 'grant-detail',
											params: { id: grant.id }}"
											class="btn btn__primary">
											View Details
										</router-link>
									</div>
								</div>
							</div>
						</template>
						<template v-else-if="grantsLoaded">
							<p>No Grant found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading grants, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
		<Pagination
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit" />
	</div>
</template>

<script>
import Pagination from '../../components/Pagination';
import Loader from '../../components/Loader';
import { getGrants } from '../../utils/api/grant';
import eventHub from '../../utils/eventhub';

export default {
	name: 'Index',
	data() {
		return {
			loadingGrants: true,
			grants: {},
			grantsLoaded: false,
			limit: 10,
			offset: 0,
			total: 0,
			baseUrl: 'grants'
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getGrants();
		eventHub.$on('grant-created', (payload) => {
			this.grants.unshift(payload);
		});
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getGrants();
		}
	},
	components: {
		Loader,
		Pagination
	},
	methods: {
		getGrants() {
			this.loadingGrants = true;
			getGrants(this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.grants = response.data.data;
						this.total = response.data.total;
						this.grantsLoaded = true;
					}
				})
				.catch((error) => {
					this.loadingGrants = false;
					this.grantsLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingGrants = false;
				});
			return true;
		}
	}
};
</script>

<style lang="scss" scoped>
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
	display: grid;
	grid-template-columns: 35px 1fr 3fr 150px;
}
.info__address {
	margin-right: 20px;
}
</style>
